
import { defineComponent } from "vue";

import sceneMixin from "./scene";
import Game from "./Game.vue";

import ButtonUI from "../components/ButtonUI.vue";

export default defineComponent({
  name: "Title",
  components: {
    ButtonUI,
  },
  ...sceneMixin,
  setup(_, { emit }) {
    const start = () => {
      emit("goScene", {
        scene: Game,
      });
    };

    return {
      start,
    };
  },
});
